body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#quiz-card {
  box-shadow: 0px 0px 100px 0px rgba(0, 0, 0, 1);
  margin: 35px 0;
}

#quiz-card .ant-btn:focus {
  border: 1px solid #d9d9d9;
  color: black;
}

#quit-button {
  font-size: 1.5em;
  height: 2.5em;
}

#quit-button:hover {
  background-color: red;
  color: white;
}

/* low resolution, 480px and below */
@media only screen and (max-width: 480px) {
  h1 {
    font-size: 3em;
  }

  #question {
    font-size: 28px;
  }

  #start-button {
    font-size: 2em;
    height: 2em;
  }

  #top-container {
    font-size: 1.5em;
  }

  .sticky-footer {
    font-size: 12px;
  }

  #quiz-card .ant-card-head-title {
    font-size: 20px;
  }

  .ant-card-actions button {
    font-size: 16px;
    height: 30px;
  }

  #steam-logo {
    width: 300px;
    padding-bottom: 25px;
  }
}

/* medium resolution, 481px to 1023px */
@media only screen and (min-width: 481px) {
  h1 {
    font-size: 4em;
  }

  #question {
    font-size: 32px;
  }

  #start-button {
    font-size: 2em;
    height: 2em;
  }

  #top-container {
    font-size: 2em;
  }

  .sticky-footer {
    font-size: 15px;
  }

  #quiz-card .ant-card-head-title {
    font-size: 22px;
  }

  .ant-card-actions button {
    font-size: 18px;
    height: 30px;
  }

  #steam-logo {
    width: 550px;
    padding-bottom: 45px;
  }
}

/* high resolution, 1024px and higher */
@media only screen and (min-width: 1024px) {
  h1 {
    font-size: 6em;
  }

  #question {
    font-size: 36px;
  }

  #start-button {
    font-size: 4em;
    height: 2em;
  }

  #top-container {
    font-size: 3em;
  }

  .sticky-footer {
    font-size: 18px;
  }

  #quiz-card .ant-card-head-title {
    font-size: 25px;
  }

  .ant-card-actions button {
    font-size: 20px;
    height: 35px;
  }

  #steam-logo {
    width: 650px;
    padding-bottom: 30px;
  }
}

/* hide footer for devices with low height screens */
@media only screen and (max-height: 450px) {
  .sticky-footer {
    display: none;
  }
}
body {
  background-color: #eee;
}

.flex-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.flex-container {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  /* height: 300px; */
}

.flex-box {
  width: 100%;
  text-align: center;
}

.sticky-footer {
  background-color: #e6e6e6;
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 10px 0;
  text-align: center;
}

.left-corner-float {
  position: absolute;
  left: 0;
  top: 0;
  padding: 15px 0 0 15px;
}

.right-corner-float {
  position: absolute;
  right: 0;
  top: 0;
  padding: 15px 25px 0 0;
}

.startButton {
  opacity: 1;
}

.startButton:hover {
  opacity: 0.8;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: all 1s;
  transition: all 1s;
}

.score-result {
  color: red;
  font-weight: bold;
}

.quit-button__content {
  padding: 5px;
}

